import React from 'react';

type Props = {
  tabs: Tab[],
  changeContent: (id:string) => void,
};

export const Tabs: React.FC<Props> = ({ tabs, changeContent }) => {
  return (
    <div className="tabs">
      <ul>
        {tabs.map(tab => (
          <li key={tab.id}>
            <button
              type="button"
              className="button button is-info-light"
              onClick={() => changeContent(tab.id)}
            >
              {tab.title}
            </button>
          </li>
        ))}
      </ul>
    </div>
  );
};
